.filter-label {
  display: block;
  margin-bottom: 35px;
}

.filter-label-text {
  display: block;
  margin-bottom: 10px;
}

.filter-input {
  min-width: 200px;
  min-height: 25px;
  padding: 0 10px;
  border: 2px solid #ffcad4;
  border-radius: 10px;
  outline: transparent;
  transition: border-color 250ms cubic-bezier(0.4, 0, 0.2, 1),
    box-shadow 250ms cubic-bezier(0.4, 0, 0.2, 1);
}

.filter-input:hover,
.filter-input:focus {
  border-color: #a4133c;
  box-shadow: 0px 0px 14px rgba(164, 19, 60, 1);
}
