.user-menu-wrapper {
  display: flex;
  gap: 30px;
  align-items: center;
}

.user-name {
  color: black;
  font-weight: 700;
  line-height: 1.14;
  letter-spacing: 0.02em;
}

.logout-btn {
  background-color: #d8e2dc;
  padding: 10px 15px;
  border: 2px solid transparent;
  border-radius: 10px;
  font-size: 16px;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  cursor: pointer;
  transition: color 250ms cubic-bezier(0.4, 0, 0.2, 1),
    border 250ms cubic-bezier(0.4, 0, 0.2, 1),
    box-shadow 250ms cubic-bezier(0.4, 0, 0.2, 1);
}

.logout-btn:hover,
.logout-btn:focus {
  border-color: #9b132d;
  color: #9b132d;
  box-shadow: 0px 0px 14px rgba(164, 19, 60, 1);
}
